interface IPoint3D {
  x: number;
  y: number;
  z: number;
}

type TVectorEntries = Array<[string, number]>;

export const vectorToEntries = (point3D: IPoint3D) => {
  return [
    ["x", point3D.x],
    ["y", point3D.y],
    ["z", point3D.z],
  ] as TVectorEntries;
};

export const sortVectorEntries = (entries: TVectorEntries) => {
  const sortOrder = "yzx";
  entries.sort(([axisA, valueA], [axisB, valueB]) =>
    Math.abs(valueA) === Math.abs(valueB)
      ? sortOrder.indexOf(axisA) - sortOrder.indexOf(axisB)
      : Math.abs(valueB) - Math.abs(valueA)
  );
  return entries;
};
