import React from "react";
import EventEmitter from "events";
import * as Three from "three";
import { isDebugActive } from "../config";
import { TInitFn, TOnAnimateFn, useThree } from "../hooks/useThree";
import defaultTexture from "../images/rubiks-cube.png";
import { defaultState, stateToCube } from "../rubiks-cube/cube-util";
import fundamentalOperations from "../rubiks-cube/fundamentalOperations";
import {
  createOperationMap,
  findOperationEntryByDifference,
  initOperationMap,
} from "../rubiks-cube/operation-util";
import {
  faceInfo,
  faceInfoEntries,
  faceNames,
} from "../rubiks-cube/spatial-util";
import { CubieGeometry } from "../threejs/CubieGeometry";
import { sortVectorEntries, vectorToEntries } from "../vector-utils";
import { ICubeHandle, ICubeProps } from "./RubiksCube.types";

type TRotateFaceFn = (
  faceIndex: number,
  quarterTurns: number,
  counterClockWise: boolean
) => Promise<void> | undefined;

interface IRotationJob {
  faceIndex: number;
  group: Three.Group;
  radian: number;
  count: number;
}

interface IParallelJobs {
  axisName: string;
  axis: Three.Vector3;
  "-1": IRotationJob;
  "1": IRotationJob;
}

type TRotateParam = [number, number, boolean];

export interface ICubeControls {
  rotate?: TRotateFaceFn;
}

const operations = initOperationMap(createOperationMap(fundamentalOperations));

const rotateParams: Record<string, TRotateParam> = {
  U: [0, 1, false],
  U2: [0, 2, false],
  "U'": [0, 1, true],
  L: [1, 1, true],
  L2: [1, 2, true],
  "L'": [1, 1, false],
  F: [2, 1, false],
  F2: [2, 2, false],
  "F'": [2, 1, true],
  R: [3, 1, false],
  R2: [3, 2, false],
  "R'": [3, 1, true],
  B: [4, 1, true],
  B2: [4, 2, true],
  "B'": [4, 1, false],
  D: [5, 1, true],
  D2: [5, 2, true],
  "D'": [5, 1, false],
};

const getCameraInfo = (camera: Three.PerspectiveCamera) => {
  return {
    position: camera.position.clone(),
    direction: camera.getWorldDirection(new Three.Vector3()),
    up: camera.up.clone(),
    quaternion: camera.quaternion.clone(),
    rotation: camera.rotation.clone(),
  };
};

const axises = {
  x: new Three.Vector3(1, 0, 0),
  y: new Three.Vector3(0, 1, 0),
  z: new Three.Vector3(0, 0, 1),
};

export const Cube3D = React.forwardRef<ICubeHandle, ICubeProps>(
  (
    { cubeState = defaultState, texture: textureProp = defaultTexture },
    forwardRef
  ) => {
    // store cube state for creating geometry
    const [initialCubeState, setInitialCubeState] = React.useState(cubeState);

    // store current cube
    const cubeRef = React.useRef(stateToCube(initialCubeState));

    const [cameraInfo, setCameraInfo] = React.useState({
      position: new Three.Vector3(),
      direction: new Three.Vector3(),
      up: new Three.Vector3(),
      quaternion: new Three.Quaternion(),
      rotation: new Three.Euler(),
    });

    const cubeControlsRef = React.useRef<Partial<ICubeControls>>({});

    const init: TInitFn = React.useCallback(
      ({ scene, camera, controls, render }) => {
        const debugActive = isDebugActive();
        const distance = 2.5;

        controls?.reset();
        scene.clear();
        scene.background = new Three.Color(0xffffff);
        if (debugActive) {
          scene.add(new Three.AxesHelper(distance));
        }

        camera.position.x = distance;
        camera.position.y = distance;
        camera.position.z = distance;
        camera.lookAt(new Three.Vector3(0, 0, 0));
        camera.updateProjectionMatrix();
        setCameraInfo(getCameraInfo(camera));

        if (controls) {
          if (debugActive) {
            scene.add((controls as any)._gizmos);
          }
          controls.enablePan = false;
          controls.minDistance = 4.33;
          controls.addEventListener("change", () => {
            setCameraInfo(getCameraInfo(camera));
          });
          controls.update();
        }

        const texture = new Three.TextureLoader().load(textureProp, () => {
          render();
        });
        const material = new Three.MeshBasicMaterial({ map: texture });

        const cubes: Three.Object3D[] = [];
        for (let x = -1; x <= 1; x++) {
          for (let y = -1; y <= 1; y++) {
            for (let z = -1; z <= 1; z++) {
              const cube = new Three.Mesh(
                new CubieGeometry({ x, y, z }, initialCubeState),
                material
              );
              scene.add(cube);
              cubes.push(cube);
              cube.position.x = x;
              cube.position.y = y;
              cube.position.z = z;
            }
          }
        }

        const dispatcher = new EventEmitter();
        const parallelJobs: IParallelJobs = {
          axisName: "",
          axis: new Three.Vector3(),
          "-1": {
            faceIndex: -1,
            group: new Three.Group(),
            radian: 0,
            count: 0,
          },
          "1": {
            faceIndex: -1,
            group: new Three.Group(),
            radian: 0,
            count: 0,
          },
        };

        const prepareRotation = (
          faceIndex: number,
          radian: number,
          count: number
        ) => {
          const face = faceInfo[faceIndex];
          parallelJobs.axisName = face.axis;
          parallelJobs.axis = axises[face.axis];

          const job = (parallelJobs as any)[face.direction];
          if (!job.count) {
            const group = new Three.Group();
            scene.add(group);
            group.add(
              ...cubes.filter((el) => el.position[face.axis] === face.direction)
            );
            job.group = group;
          }

          job.radian = radian;
          job.count += count;
        };

        const applyRotation = (axis: Three.Vector3, job: IRotationJob) => {
          if (!job.count) {
            return;
          }
          const sign = Math.sign(job.count);
          job.group.rotateOnAxis(axis, job.radian * sign);
          job.count -= sign;
          if (!job.count) {
            finishRotation(job.group);
            dispatcher.emit(`finishRotation.${job.faceIndex}`);
          }
        };

        const finishRotation = (group: Three.Group) => {
          for (const cube of [...group.children]) {
            const position = cube.getWorldPosition(new Three.Vector3());
            const quaternation = cube.getWorldQuaternion(
              new Three.Quaternion()
            );
            scene.add(cube);
            cube.position.copy(position);
            cube.rotation.setFromQuaternion(quaternation);
            cube.position.x = Math.round(cube.position.x);
            cube.position.y = Math.round(cube.position.y);
            cube.position.z = Math.round(cube.position.z);
          }
          group.removeFromParent();
        };

        const rotate = (
          faceIndex: number = faceNames.U,
          quarterTurns: number = 1,
          counterClockWise: boolean = false
        ) => {
          const face = faceInfo[faceIndex];
          const steps = 25;
          const radian = Math.PI / 2 / steps;
          const count = steps * quarterTurns * (counterClockWise ? 1 : -1);

          if (parallelJobs.axisName && parallelJobs.axisName !== face.axis) {
            console.log("is busy");
            return new Promise<void>((resolve) => {
              dispatcher.once("finishRotation", () => {
                rotate(faceIndex, quarterTurns, counterClockWise).then(resolve);
              });
            });
          }

          // prepare group
          prepareRotation(faceIndex, radian, count);

          // wait for animation loop finished
          return new Promise<void>((resolve) => {
            dispatcher.once(`finishRotation.${faceIndex}`, () => {
              resolve();
            });
          });
        };

        cubeControlsRef.current.rotate = rotate;

        render();

        const onAnimate: TOnAnimateFn = ({ render }) => {
          applyRotation(parallelJobs.axis, parallelJobs["-1"]);
          applyRotation(parallelJobs.axis, parallelJobs["1"]);
          render();

          if (
            parallelJobs.axisName &&
            !parallelJobs["-1"].count &&
            !parallelJobs["1"].count
          ) {
            parallelJobs.axisName = "";
            dispatcher.emit("finishRotation");
          }
        };

        return {
          onAnimate,
        };
      },
      [textureProp, initialCubeState]
    );

    React.useEffect(() => {
      // new cube
      const cubeB = stateToCube(cubeState);
      // current cube
      const cubeA = cubeRef.current;
      // always update current cube
      cubeRef.current = cubeB;
      // compare cubes find applied operation
      const operation = findOperationEntryByDifference(
        operations,
        cubeB,
        cubeA
      );
      if (!operation) {
        // recreate cube geometry when operation not found
        setInitialCubeState(cubeState);
        return;
      }
      const [operationName] = operation;
      if (!(operationName in rotateParams)) {
        // recreate cube geometry when imperative api for operation doesn't exists
        setInitialCubeState(cubeState);
        return;
      }
      // animate changes
      cubeControlsRef.current.rotate?.(...rotateParams[operationName]);
    }, [cubeState]);

    React.useImperativeHandle(
      forwardRef,
      () => ({
        cubeState,
      }),
      [cubeState]
    );

    const { reactElement } = useThree(350, 275, init);
    const lookAtEntry = sortVectorEntries(
      vectorToEntries(cameraInfo.direction.clone().negate())
    )[0] as [string, number];
    const lookAtAxis = lookAtEntry[0];
    const lookAtSign = lookAtEntry[1] < 0 ? -1 : 1;
    const lookAtFaceEntry = faceInfoEntries.find(
      ([k, v]) => v.axis === lookAtEntry[0] && v.direction === lookAtSign
    )!;
    const lookAtFaceIndex = Number(lookAtFaceEntry[0]);
    const lookAtFaceName = Object.entries(faceNames).find(
      ([k, v]) => v === lookAtFaceIndex
    )![0];

    const upEntry = sortVectorEntries(
      vectorToEntries(cameraInfo.up).filter(([k]) => k !== lookAtAxis)
    )[0] as [string, number];
    const upSign = upEntry[1] < 0 ? -1 : 1;
    const upFaceEntry = faceInfoEntries.find(
      ([k, v]) => v.axis === upEntry[0] && v.direction === upSign
    )!;
    const upFaceIndex = Number(upFaceEntry[0]);
    const upFaceName = Object.entries(faceNames).find(
      ([k, v]) => v === upFaceIndex
    )![0];

    return (
      <div
        style={{
          position: "relative",
          userSelect: "none",
        }}
      >
        {reactElement}
        {isDebugActive() && (
          <div
            style={{
              position: "absolute",
              top: "0px",
              left: "0px",
              color: "red",
              fontSize: "0.5rem",
            }}
          >
            Camera position: ({Math.floor(10 * cameraInfo.position.x) / 10},{" "}
            {Math.floor(10 * cameraInfo.position.y) / 10},{" "}
            {Math.floor(10 * cameraInfo.position.z) / 10})
            <br />
            Camera direction: ({Math.floor(10 * cameraInfo.direction.x) /
              10}, {Math.floor(10 * cameraInfo.direction.y) / 10},{" "}
            {Math.floor(10 * cameraInfo.direction.z) / 10})
            <br />
            Up: {upFaceName}
            <br />
            Front: {lookAtFaceName}
          </div>
        )}
      </div>
    );
  }
);
